.section__about {
  background-color: var(--main-background-color);

  @media screen and (max-width: 767px) {
    background-image: url('../images/mob/howit-bkg@1x.png');
    background-size: contain;
    background-repeat: no-repeat;
    background-position: 50% 83%;
    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: url('../images/mob/howit-bkg@2x.png');
    }
  }

  @media screen and (min-width: 1280px) {
    background-image: url('../images/desk/howit-bkg@1x.png');
    background-size: contain;
    background-repeat: no-repeat;
    background-position: bottom;
    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: url('../images/desk/howit-bkg@2x.png');
    }
  }
  
}

.about__pretitle {
  font-family: Titan One, sans-serif;
  @include font($fw: 400, $fz: 18px, $lh: 21px, $ls: 0.04em, $tt: uppercase);
  text-align: center;
  color: var(--secondary-color);
  margin-bottom: 15px;
  @media screen and (min-width: 768px) {
    @include font($fw: 400, $fz: 26px, $lh: 30px, $ls: 0.04em, $tt: uppercase);
  }
  @media screen and (min-width: 1280px) {
    @include font($fw: 400, $fz: 30px, $lh: 34px, $ls: 0.04em, $tt: uppercase);
  }
}

.about__title {
  font-family: Titan One, sans-serif;
  text-align: center;
  color: var(--primary-color);
  @include font($fw: 500, $fz: 34px, $lh: 39px, $ls: 0.04em, $tt: uppercase);
  @media screen and (max-width: 767px) {
    margin-bottom: 26px;
  }
  @media screen and (min-width: 768px) {
    @include font($fw: 500, $fz: 48px, $lh: 55px, $ls: 0.04em, $tt: uppercase);
    margin-bottom: 50px;
  }
  @media screen and (min-width: 1280px) {
    @include font($fw: 500, $fz: 58px, $lh: 55px, $ls: 0.04em, $tt: uppercase);
    margin-bottom: 55px;
  }
}

.wrap__box {
  @media screen and (min-width: 768px) {
    display: flex;
  }
}

.about__img {
  @media screen and (max-width: 767px) {
    margin: 0 auto;
    margin-bottom: 46px;
  }
  @media screen and (min-width: 768px) {
    margin-right: 17px;
  }
  @media screen and (min-width: 1280px) {
    margin-right: 74px;
  }
}

.wrap__box-text {
  @media screen and (min-width: 768px) {
    padding-top: 50px;
  }
}

