.about__item {
  &:not(:last-child) {
    margin-bottom: 30px;
  }
  @media screen and (max-width: 767px) {
    @include centred();
  }
}

.about__text {
  @include font($fw: 500, $fz: 12px, $lh: 19px);
  @media screen and (max-width: 767px) {
    width: 240px;
  }
  @media screen and (min-width: 768px) {
    @include font($fw: 500, $fz: 14px, $lh: 25px);
    width: 350px;
  }
  @media screen and (min-width: 1280px) {
    @include font($fw: 500, $fz: 14px, $lh: 27px);
    width: 420px;
  }
}
.about--dark {
  color: #000;
}


